.container-hero {
    padding: 6.5rem 2rem;
    display: grid;
    grid-template-columns: 1fr 3fr 1fr;
}
.wrapper {
    display: flex;
    align-items:baseline;
    justify-content: center;
    position: relative;
}

.wrapper > img {
    width: 30rem;
    position: absolute;
    bottom: 0;
}

.blueCircle {
    width: 30rem;
    height: 30rem;
    border-radius: 50%;
    z-index: -99;
    position: absolute;
    bottom: 0;
    background: linear-gradient(135.74deg, #0bb5ff 14.09%, #6dd3fe 83.81%);
}

.cart2 {
    position: absolute;
    bottom: 25%;
    right: 5%;
    display: flex;
    gap: 1rem;
    align-items: center;
}
.cart2 .shoppingbag {
    height: 4em;
    width: 4em;
}

.cart2>svg {
    width: 30px;
    height: 30px;
    background: white;
    padding: 10px;
    border-radius: 50%;
    border: 6px solid var(--black);
}
.signup {
    display: flex;
    gap: 1rem;
    align-items: center;
    background: white;
    padding: 10px;
    font-size: 0.8rem;
    border-radius: 15px;
    box-shadow: var(--shadow1);
}

.signup > :first-child {
    display: block;
    width: 6rem;

}

.signup > :nth-child(2) {
    border-radius: 50%;
    border: 1px solid skyblue;
    display: flex;
    width: 20px;
    height: 20px;
    padding: 5px;
    justify-content: center;
    align-items: center;
}





.h_sides {
    display: grid;
    grid-template-rows: 1fr 1fr;
}

.text1 {
    text-transform: uppercase;
    font-size: 1.5rem;
    width: min-content;
    display: block;
    font-weight: 600;
}

.text2 {
    display: flex;
    flex-direction: column;
    width: min-content;
    color: black;
}


.text2>:first-child {
    font-weight: 800;
    font-size: 2.5rem;
}
.text2 > :nth-child(2) {
    display: block;
}
.traffic {
    display: flex;
    flex-direction: column;
    text-align: right;
}
.traffic > :first-child {
    font-weight: 800;
    font-size: 2.5rem;
}
.customers {
    display: flex;
    flex-direction: column;
    text-align: right;
}

.customers > :first-child {
    font-weight: 800;
    font-size: 2.5rem;
}

/* media queries tablet */
@media screen and (max-width: 856px) {
  .text1, .text2 > span:nth-of-type(2) {
    font-size: 0.9rem;
  }
  .text2 > span:nth-of-type(1),
  .traffic > span:nth-child(1),
  .customers > span:nth-child(1) {
    font-size: 1.5rem;
  }

    .blueCircle {
    width: 25rem;
    height: 25rem;
    }

    .wrapper > img {
        width: 25rem;
    }
    .cart2 {
        bottom: 8%; 
        gap: 0.5rem;
    }
    .cart2 .shoppingbag {
        height: 3em;
        width: 3em;
    }
    .signup {
        gap: 0.5rem;
        padding: 8px;
        font-size: 0.6rem;
    }
}

@media screen and (max-width: 640px) {
    .wrapper {
        grid-template-columns: 1fr;
    }

    .blueCircle {
        width: 24rem;
        height: 24rem;
        }

    .wrapper > img {
        width: 60vw;
    }

    .wrapper>:nth-child(3), 
    .wrapper>:nth-child(1) {
        text-align: center;
    }
    .cart2 {
        bottom: 5%;
    }
}
