.container-header {
    padding: 1.5rem;
    display: flex;
}
.logo {
    display: flex;
    align-items: center;
    flex: 1;
    gap: 0.5rem;
}
.logo>img {
    width: 2.5rem;
    height: 2.5rem;
}
.logo>span {
    font-weight: 600;
}

.nav {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

.nav .bars {
    display: none;
}

.menu {
    display: flex;
    gap: 2rem;
    font-weight: 500;
    list-style: none;
}

.menu>li {
    transition: 0.8s ease;
}

.menu>li:hover {
    color: #fe956f;
    cursor: pointer;
}

.search {
    width: 7rem;
    height: 30%;
    outline: none;
    border: 1px solid #fff;
    border-radius: 5px;
    padding: 0.5rem;

}

.cart {
    width: 1.5rem;
    height: 1.5rem;
    cursor: pointer;
}

@media screen and (max-width: 640px) {
    .nav {
        position: absolute;
        right: 2rem;
        z-index: 999;
        background: white;
        border-radius: 10px;
        color: var(--black);
        flex-direction: column;
        padding: 1rem;
    }
    .menu {
        flex-direction: column;
        display: none;
        margin-left: 1rem;
    }
    .nav .bars {
        display: block;
    }
    .nav .search {
        display: none;
    }
}