.footer-wrapper {
    width: 100%;
    margin-top: 5rem;
    padding: 0 2rem;
}
.footer-wrapper > hr {
    width: 100%;
    height: 1px;
    border: none;
    background: rgba(201, 204, 3, 0.992);
    margin-bottom: 1rem;
}

.main-footer {
    display: flex;
    width: 100%;
    justify-content: space-around;
    padding-top: 1rem;
}
.logo-footer {
    display: flex;
    align-items: flex-start;
    gap: 1rem;
}

.logo-footer > img {
    width: 2.5rem;
    height: 2.5rem;
}

.logo-footer > span {
    font-weight: 600;
    font-size: 1rem;
    padding-top: 10px;
}

.block {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 2rem;
    max-width: 12rem;
}

.detail {
    display: flex;
    flex-direction: column;
    width: inherit;
    font-size: .7rem;
    gap: 1rem;
}

.detail > span:nth-of-type(1) {
    font-weight: 800;
    font-style: normal;
    font-size: 1.2rem;
}

.pngLine:hover {
    cursor: pointer;
}

.pngLine {
    display: flex;
    gap: 0.7rem;
    align-items: center;
}

.icon {
    width: 25px;
}

.copyRight {
    display: flex;
    flex-direction: column;
    width: 100%;
    text-align: center;
    font-size: .65rem;
    margin-top: 2rem;
}

@media screen and (max-width: 640px){
    .logo-footer {
        display: none;
    }
}

@media screen and (max-width: 768px){
    .main-footer {
        justify-content: flex-start;
        align-items: flex-start;
        gap: 2rem;
    }
    .logo-footer {
        display: none;
    }
}