.virtual {
    margin-top: 6rem;
    margin-bottom: 6rem;
    padding: 2rem 11.5rem;
    display: flex;
    justify-content: space-between;
}

.left {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    position: relative;
}

.left > :nth-child(1) {
    font-size: 1.8rem;
    text-transform: uppercase;
    display: block;
    width: 20rem;
    font-weight: bold;
}

.left > :nth-child(2) {
    font-size: 1.6rem;
    text-transform: uppercase;
    display: block;
    width: 20rem;
}

.left > :nth-child(3) {
    font-size: 1.6rem;
    font-weight: bold;
}

.left > img {
    position: absolute;
    width: 10rem;
    bottom: 6.4rem;
    z-index: -1;
}

.wrapper-virtual {
    width: 30rem;
}

@media screen and (max-width: 856px){
    .virtual {
        gap: 1rem;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin-top: 2rem;
        margin-bottom: 2rem;
        padding: 1rem;
        text-align: center;
    }
}